export default {
	props: ['index'],
	data() {
		return {

		}
	},
	methods: {
		selectTabbar(index) {
			if (index == 1) {
				uni.navigateTo({
					url: '../../pages/add/add'
				})
				return;
			}
			this.$store.commit("changeTabbar", index);
		}
	},
	created() {
		if (this.index) {
			this.$store.commit("changeTabbar", JSON.parse(this.index))
		}
	},
	computed: {
		tabbarList() {
			return this.$store.state.tabbar.tabbarList;
		},
		tabbarIndex() {
			return this.$store.state.tabbar.index;
		}
	}
}
